.navBar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  z-index: 1;
  width: 100%;
  height: 60px;
  background-color: #29188b;
}

@media (max-width: 700px) {
  .navBar {
    display: block;
    text-align: center;
  }
}

.navBar .title {
  color: #7cc856;
  margin-left: 20px;
}

.navBar .list {
  list-style: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  margin-right: 20px;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}

@media (max-width: 700px) {
  .navBar .list {
    text-align: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
  }
}

.navBar .list .navlink {
  color: white;
  margin: 10px;
  font-size: 18px;
  text-decoration: none;
  color: #7cc856;
}

.navBar .list a.selected {
  color: #811a3c;
  cursor: default;
}
